body .loading-mask {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65) url(./45297c31ffa896365096625c6a352659.svg) no-repeat center center;
  background-size: 1rem auto;
  z-index: 400;
  left: 0;
  top: 0;
