body .loading-mask {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65) url(./45297c31ffa896365096625c6a352659.svg) no-repeat center center;
  background-size: 1rem auto;
  z-index: 400;
  left: 0;
  top: 0;
  color: #fff;
  font-size: 0.16rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .loading-mask .loading-text {
  margin-top: 1rem;
  -webkit-animation: blink 2s 0s linear infinite;
  -webkit-animation-fill-mode: forwards;
  -moz-animation: blink 2s 0s linear infinite;
  -ms-animation: blink 2s 0s linear infinite;
}
body .loading-mask .loading-close {
  width: 0.3rem;
  height: 0.3rem;
  background: rgba(255, 255, 255, 0.6) url(./b81ec460fa214c7304087c8c384fa698.png) no-repeat center center;
  background-size: 0.15rem auto;
  position: absolute;
  right: 0.15rem;
  top: 0.15rem;
  border-radius: 50%;
}
body .mask {
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65);
  z-index: 300;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 200ms 0s linear;
}
body .mask.dialog-layer .dialog {
  display: block;
  border: 0;
  width: 4.7rem;
  background: #fff;
  border-radius: 0.3rem;
  box-shadow: 0px 2px 12px 0px rgba(0, 0, 0, 0.29);
  overflow: hidden;
  height: auto;
  transform: scale(0);
  transition: all 200ms 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
body .mask.dialog-layer .dialog .title {
  width: 4.7rem;
  height: 0.7rem;
  font-size: 0.22rem;
  font-weight: 600;
  color: #fbab22;
  line-height: 0.7rem;
  text-align: center;
  position: relative;
  border-bottom: 0.01rem solid #d2d2d2;
}
body .mask.dialog-layer .dialog .title .title-text {
  width: 1.2rem;
  height: 100%;
  line-height: 0.8rem;
  border-bottom: 0.02rem solid #fbab22;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -0.6rem;
  margin-top: -0.35rem;
  font-weight: 600;
  font-size: 0.22rem;
}
body .mask.dialog-layer .dialog .title .close-btn {
  width: 0.2rem;
  height: 0.2rem;
  background: url(./0f1cd2c842b50b020eb5f8916e128be1.png) no-repeat center center;
  background-size: 0.16rem auto;
  position: absolute;
  right: 0.2rem;
  top: 0.2rem;
}
body .mask.dialog-layer .dialog .title.clear {
  background: rgba(0, 0, 0, 0);
}
body .mask.dialog-layer .dialog .content {
  display: block;
  margin: 0;
  padding-bottom: 0.3rem;
  height: auto;
}
body .mask.dialog-layer .dialog .content.nobtn {
  padding-bottom: 0;
}
body .mask.dialog-layer .dialog .content .texts {
  line-height: 2;
  font-size: 0.2rem;
  color: #666;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.36rem 0.4rem;
  box-sizing: border-box;
}
body .mask.dialog-layer .dialog .content .texts.large {
  padding: 0.4rem 0.3rem;
}
body .mask.dialog-layer .dialog .content .btns {
  display: flex;
  justify-content: space-around;
}
body .mask.dialog-layer .dialog .content .btns button {
  width: 1.5rem;
  height: 0.6rem;
  color: #fff;
}
body .mask.dialog-layer .dialog .content .btns button.ok-btn {
  background: url(./d8ab9784c6e75f0f4f732a261acf6c50.png) no-repeat;
  background-size: 100% 100%;
}
body .mask.dialog-layer .dialog .content .btns button.ok-btn.check-again {
  background: url(./9ef017e3eb50b27a7a287ee3a5f4ca0a.png) no-repeat;
  background-size: 100% 100%;
}
body .mask.dialog-layer .dialog .content .btns button.ok-btn.changepwd {
  width: 2.4rem;
  height: 0.6rem;
  margin-left: -3.8rem;
  background: url(./5afde3660b40a4b84cd6d6a371cabae9.png) no-repeat;
  background-size: 100% 100%;
}
body .mask.dialog-layer .dialog .content .btns button.ok-btn.end-class {
  background: url(./e4756e4c5fea15216801de4d643f7ab1.png) no-repeat;
  background-size: 100% 100%;
}
body .mask.dialog-layer .dialog .content .btns button.cancel-btn {
  background: url(./2b206bd9695e74211e0d3de504a5f241.png) no-repeat;
  background-size: 100% 100%;
}
body .mask.dialog-layer .dialog .content .btns button.cancel-btn.check-jump {
  background: url(./dd24af4347b051b5ce778784bf8e3e7a.png) no-repeat;
  background-size: 100% 100%;
}
body .mask.dialog-layer .dialog .content .btns button.cancel-btn.none {
  background: none;
}
body .mask.dialog-layer .dialog .content .btns button.cancel-btn.leave-class {
  background: url(./509e4308a443794071af3ac0758efbb8.png) no-repeat;
  background-size: 100% 100%;
}
body .mask.dialog-layer .dialog.large {
  width: 7.15rem;
}
body .mask.show {
  opacity: 1;
}
body .mask.show .dialog {
  transform: scale(1);
}
body .mask.hide {
  visibility: hidden;
}
body .calendar-box {
  font-size: 0.17rem;
  color: #000000;
  width: 8.2rem;
  margin: 0 auto 0.3rem;
  background: #fff;
}
body .cal-bd {
  border-bottom: 0.01rem solid #cccccc;
}
body .highlight {
  color: #ff2a2a;
}
body .calendar-head {
  height: 0.46rem;
  display: flex;
  align-items: center;
}
body .calendar-head span {
  flex: 1;
  text-align: center;
}
body .calendar-control {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 0.5rem;
  color: #000;
}
body .calendar-control .control-l,
body .calendar-control .control-r {
  width: 0.6rem;
  height: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .calendar-control .control-l img,
body .calendar-control .control-r img {
  width: 0.07rem;
}
body .calendar-control .control-r img {
  transform: scaleX(-1);
}
body .calendar-control .control-c {
  text-align: center;
  margin: 0 0.3rem;
}
body .calendar-row {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 0.46rem;
}
body .calendar-col {
  width: 0.33rem;
  height: 0.33rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: relative;
}
body .calendar-col.disabled {
  color: #fff;
  background: #cacaca;
}
body .calendar-col.selected {
  color: #fff;
  background: #ff2a2a;
}
body .calendar-col.highlighted::after {
  content: "";
  position: absolute;
  width: 0.06rem;
  height: 0.06rem;
  border-radius: 50%;
  background: #ff2a2a;
  bottom: 0;
  left: 50%;
  margin-left: -0.03rem;
}
body .toast-box {
  width: 100%;
  height: 0.5rem;
  position: fixed;
  bottom: 1rem;
}
body .toast-box .toast {
  position: relative;
}
body .toast-box .toast p {
  background: #3e7c9c;
  border-radius: 0.2rem;
  color: #fff;
  font-size: 0.14rem;
  padding: 0.14rem 0.26rem;
  box-sizing: border-box;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
html,
body {
  height: 100%;
}
html,
body {
  background: #ffffff;
  color: #fff;
}
html #app,
body #app,
html .page,
body .page {
  width: 100%;
  height: 100%;
}
html .page,
body .page {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
html .close-btn,
body .close-btn {
  width: 15px;
  height: 15px;
  background: url(./0f1cd2c842b50b020eb5f8916e128be1.png) no-repeat center center;
  background-size: 100% auto;
  position: absolute;
  right: 10px;
  top: 10px;
}
html .logo,
body .logo {
  width: 72px;
  height: 72px;
  background: url(./b49a806ea8f422fa0b5e11d8dde3247d.png) no-repeat center center;
  background-size: 100% auto;
  margin-bottom: 22px;
  border-radius: 12px;
}
html .tips,
body .tips {
  font-size: 18px;
  color: rgba(0, 0, 0);
  max-width: 400px;
  text-align: center;
  line-height: 1.5;
}
html .progress,
body .progress {
  margin-top: 20px;
  width: 315px;
  height: 20px;
  display: flex;
  align-items: center;
}
html .progress .bar,
body .progress .bar {
  margin-right: 10px;
  width: 270px;
  height: 14px;
  border-radius: 14px;
  background: #ebebeb;
}
html .progress .bar .bar-i,
