  border-radius: 14px;
  background: #3fb7fc;
  height: 100%;
}
html .progress .txt,
body .progress .txt {
  font-size: 14px;
  color: rgba(0, 0, 0);
}
html .restart-btn,
body .restart-btn {
  width: 80px;
  height: 32px;
  font-size: 14px;
  border-radius: 5px;
  color: rgba(148, 86, 4);
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(252, 201, 63);
  cursor: pointer;
  margin-top: 20px;
}
html .restart-btn:hover,
body .restart-btn:hover {
  background: rgba(255, 255, 255, 0.8);
}

